<style scoped>
</style>
<style>
</style>
<template>
  <div :class="{
        'amiui-cell': true,
        'amiui-cell_access': $_access,
        'amiui-cell_active': active,
        'amiui-cell_swiped': $_swiped
       }">
    <div class="amiui-cell__hd"
         v-if="showHd">
      <i v-if="icon" :class="icon"></i>
      <slot name="hd"></slot>
      {{ hd }}
    </div>
    <div class="amiui-cell__bd"
         @touchstart="_touchStart"
         @touchmove="_touchMove"
         @touchend="_touchEnd"
         :style="swipeStyle"
         v-if="showBd">
      <template v-if="$_swiped">
        <am-cell :bd="bd" :ft="ft" :access="$_access">
          <slot></slot>
          <slot name="ft"></slot>
        </am-cell>
      </template>
      <template v-else>
        <slot></slot>
        {{ bd }}
      </template>
    </div>
    <div class="amiui-cell__ft"
         v-if="showFt">
      <template v-if="$_swiped">
        <a class="amiui-swiped-btn amiui-swiped-btn_warn"></a>
      </template>
      <template v-else>
        <slot name="ft"></slot>
        {{ ft }}
      </template>
    </div>
  </div>
</template>
<script>
  import './cell.css';

  export default {
    name: 'AmCell',
    props: {
      active: {     //cellsactiveType 
        type: Boolean, 'default': false
      },
      access: {      //cell
        type: Boolean, 'default': false
      },
      hd: {
        type: String, 'default': ''
      },
      bd: {
        type: String, 'default': ''
      },
      ft: {
        type: String, 'default': ''
      },
      icon: {
        type: String, 'default': ''
      },
      swipedBtn: {       //
        type: Boolean, 'default': false
      }
    },
    watch: {},
    data() {
      return {
        startX: 0,       //
        moveX: 0,        //
        disX: 0,         //
        delWidth: 60,    //
        swipeStyle: '',
      }
    },
    computed: {
      $_isCells() {
        return this.$parent.$options._componentTag === 'am-cells'
      },
      $_access() {
        return this.access ||
          (this.$_isCells ? this.$parent.access : false)
      },
      $_activeType() {
        return this.$_isCells ? this.$parent.activeType : false
      },
      $_swiped() {
        return this.swipedBtn ||
          (this.$_isCells ? this.$parent.swipedBtn : false)
      },
      showHd() {
        return this.hd || this.$_activeType || this.icon || this.$slots.hd
      },
      showBd() {
        return this.bd || this.$slots.default
      },
      showFt() {
        return this.ft || this.$_access || this.$slots.ft || this.$_swiped
      }
    },
    methods: {
      _touchStart(e) {
        if (this.$_swiped && e.touches.length === 1) {
          this.startX = e.touches[0].clientX;
        }
      },
      _touchMove(e) {
        if (this.$_swiped && e.touches.length === 1) {
          this.moveX = e.touches[0].clientX;
          this.disX = this.startX - this.moveX;
          if (this.disX <= 0) {
            this.swipeStyle = "transform:translateX(0px)";
          } else if (this.disX > 0) {
            this.swipeStyle = "transform:translateX(-" + this.disX + "px)";
            if (this.disX >= this.delWidth/ 2) {
              this.swipeStyle = "transform:translateX(-" + this.delWidth + "px)";
            }
          }
        }
      },
      _touchEnd(e) {
        if (this.$_swiped && e.changedTouches.length === 1) {
          let endX = e.changedTouches[0].clientX;
          this.disX = this.startX - endX;
          if (this.disX < this.delWidth / 2) {
            this.swipeStyle = "transform:translateX(0px)";
          }
          this.startX = 0;
        }
      },
    },
    created() {

    },
    mounted() {
    }
  }
</script>


