/**
 * 
 * sync visible show propsvisible
 */
export default {
  props: {
    visible: {
      type: Boolean, 'default': false
    },
  },
  computed: {
    show: {
      get() {
        return this.visible
      },
      set(s) {
        this.$emit('update:visible', s);
      }
    }
  }
}


